import React from 'react';
import PropTypes from 'prop-types';
import Link from 'gatsby-link';
import Headroom from 'react-headroom';
import Helmet from 'react-helmet';
import { MuiThemeProvider, createMuiTheme } from 'material-ui/styles';
import Typography from 'material-ui/Typography';

import AppBar from '../components/app-bar';
import Footer from '../components/footer';
import palette from '../styles/mui-palette';
import typography from '../styles/mui-typography';
import PoweredByStrava from '../assets/api_logo_pwrdBy_strava_horiz_gray.svg';
import ConnectWithStrava from '../assets/btn_strava_connectwith_light.svg';

const theme = createMuiTheme({ palette, typography });

export default class Template extends React.Component {
  static propTypes = {
    children: PropTypes.func,
  }

  componentDidMount() {
    document.body.style.margin = '0 auto';
    document.body.style.paddingTop = 0;
  }

  render() {
    return (
      <MuiThemeProvider theme={theme} >
        <Typography component="body1" >
          <div>
            <Helmet
              title="A Race athlete"
              meta={[
                { name: 'description', content: 'Using your Strava cycling metrics to help you reach your goals' },
                { name: 'keywords', content: 'cycling, endurance, training, goals, motivation' },
              ]}
            />
            {this.props.children()}
          </div>
        </Typography>
      </MuiThemeProvider>
    );
  }
}



